.container{
  padding:0 15px
}

body{
  color:#133241;
  font-family:'LondrinaSolid-Light'
}

header{
  padding:50px 0 10px 0
}

.roadmap .roadmap-item{
  padding-bottom:70px;
  padding-left:150px;
  position:relative
}

@media only screen and (max-width: 600px){
  .roadmap .roadmap-item{
    padding-left:100px;
    padding-bottom:130px
  }
}

.roadmap .roadmap-item:before{
  content:'';
  position:absolute;
  width:76px;
  height:90px;
  border-radius:50%;
  background:white;
  left:0;
  top:0
}

.roadmap .roadmap-item:after{
  content:'';
  position:absolute;
  left:35px;
  top:70px;
  height:100%;
  width:10px;
  background:white
}

.roadmap .roadmap-item:last-child:after{
  display:none
}

.roadmap .roadmap-item .roadmap-item-image{
  position:absolute;
  bottom:-85px;
  right:100px
}

@media only screen and (max-width: 600px){
  .roadmap .roadmap-item .roadmap-item-image{
    right:0px;
    bottom:30px
  }
}

.roadmap .roadmap-item .roadmap-item-image img{
  width:300px
}

@media only screen and (max-width: 600px){
  .roadmap .roadmap-item .roadmap-item-image img{
    width:200px
  }
}

select{
  background-image:url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' fill='none' viewBox='0 0 20 20'%3e%3cpath stroke='white' stroke-linecap='round' stroke-linejoin='round' stroke-width='1.5' d='M6 8l4 4 4-4'/%3e%3c/svg%3e")
}

footer{
  padding:60px 0 40px 0
}

.banner-wrapper{
  padding:100px 0;
  position:relative;
  background-color:#20B3FF;
  background-image:url(/static/media/banner-character-2.088dcda0.png);
  background-repeat:no-repeat;
  background-size:contain;
  background-position:right;
  min-height:660px
}

@media only screen and (max-width: 600px){
  .banner-wrapper{
    min-height:0
  }
}

