  .md\:justify-end{
    justify-content: flex-end;
  }

  .md\:pt-36{
    padding-top: 9rem;
  }

  .md\:pb-80{
    padding-bottom: 20rem;
  }

  .md\:text-left{
    text-align: left;
  }

  .md\:text-sm{
    font-size: 0.875rem;
    line-height: 1.25rem;
  }

  .md\:text-lg{
    font-size: 1.125rem;
    line-height: 1.75rem;
  }

  .md\:text-2xl{
    font-size: 1.5rem;
    line-height: 2rem;
  }

  .md\:text-4xl{
    font-size: 2.25rem;
    line-height: 2.5rem;
  }
}

